body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'Alien Robot Regular';
font-style: normal;
font-weight: normal;
src: local('Alien Robot Regular'), url("/static/media/Alien Robot.22e05b86.woff") format('woff');
}

h1 {
	font-family: 'Alien Robot Regular'; 
	font-weight: 200;
	color: #0ccac4; 
}


